.gl-layout-content {
  flex: 1 1 100%;
  display: flex;
  flex-direction: column;
}

.gl-modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  opacity: 0;
  transition: 400ms opacity;
  background-color: rgba(0, 0, 0, 0.2);
  pointer-events: none;
  z-index: var(--z-index-modal);
}
.mod-show.gl-modal {
  opacity: 1;
  pointer-events: auto;
}

button.mod-loading {
  position: relative;
  color: transparent;
  cursor: default;
}
button.mod-loading:after {
  display: block;
  content: "";
  width: 1.2em;
  height: 1.2em;
  position: absolute;
  top: calc(50% - 0.55em);
  left: calc(50% - 0.6em);
  border: 0.15em solid white;
  border-radius: 0.6em;
  border-left-color: transparent;
  border-bottom-color: transparent;
  animation: load 1s linear infinite;
}

.loader {
  display: inline-block;
  width: 1em;
  height: 1em;
  border-radius: 0.5em;
  border: 0.1em solid white;
  border-left-color: transparent;
  border-bottom-color: transparent;
  animation: load 1s linear infinite;
}

@keyframes load {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
