.gl-swiper__swiper-wrapper {
  display: flex;
  align-items: stretch;
  position: relative;
  width: 100%;
  height: 100%;
  z-index: 1;
}
.gl-swiper__btn-nav {
  position: absolute;
  top: 50%;
  display: block;
  width: 40px;
  height: 40px;
  background-image: url("../img/arrow-translucent.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: 20px;
  z-index: 1;
  border-radius: var(--border-radius-small);
  background-color: transparent;
  transition: all var(--transition-duration-short);
}
.gl-swiper__btn-nav:hover {
  background-image: url("../img/arrow-wite.svg");
  background-color: var(--gray-color-opasity-2);
}
.gl-swiper__btn-nav:disabled {
  display: none;
}
.gl-swiper__btn-nav--prev {
  transform: translateY(-50%) rotate(90deg);
  left: 0;
}
@media screen and (max-width: 767px) {
  .gl-swiper__btn-nav--prev {
    left: -10px;
  }
}
.gl-swiper__btn-nav--next {
  transform: translateY(-50%) rotate(-90deg);
  right: 0;
}
@media screen and (max-width: 767px) {
  .gl-swiper__btn-nav--next {
    right: -10px;
  }
}
.gl-swiper__btn-nav + .gl-swiper__btn-nav {
  margin-left: 8px;
}
.gl-swiper__btn-nav.swiper-button-disabled {
  opacity: 0.7;
}
.gl-swiper__pagination-wrapper {
