.header__burger-btn {
  position: relative;
  display: block;
  width: 28px;
  height: 28px;
  /* верхняя и нижняя линии бургера */
  /* средняя линия бургера */
}
.header__burger-btn:before, .header__burger-btn:after {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 16px;
  transition: all var(--transition-duration-short) ease;
  margin-left: calc((28px - 16px) / 2);
}
.header__burger-btn:before {
  top: calc(
        28px / 2 -
        2px / 2 -
        4px -
        2px
      );
  height: calc(
        2px * 3 +
        4px * 2
      );
  border-top: 2px solid var(--white-color);
  border-bottom: 2px solid var(--white-color);
}
.header__burger-btn:after {
  top: calc(
        28px / 2 -
        2px / 2
      );
  height: 2px;
  background-color: var(--white-color);
}
.header__burger-btn.mod-close:before, .header__burger-btn.mod-close:after {
  top: 50%;
  width: 20px;
}
.header__burger-btn.mod-close:before {
  border: none;
  height: 2px;
  background: var(--white-color);
  transform: rotate(45deg);
}
.header__burger-btn.mod-close:after {
  transform: rotate(-45deg);
}

