  .statistics {
    grid-column: 1/25;
  }
}
@media screen and (max-width: 767px) {
  .statistics {
    grid-column: 1;
    width: calc(100vw - (var(--page-content-padding)) * 2);
    padding-right: 0;
  }
}
.statistics--right {
  grid-column: 13/25;
}
@media screen and (max-width: 1279px) {
  .statistics--right {
    grid-column: 1/25;
  }
}
@media screen and (max-width: 767px) {
  .statistics--right {
    grid-column: 1;
  }
}
.statistics__header {
  display: flex;
  justify-content: space-between;
  margin-bottom: 30px;
}
@media screen and (max-width: 767px) {
  .statistics__header {
    padding-right: var(--block-padding-side);
  }
}
.statistics__more-link {
  padding: 3px 16px 3px 6px;
  font-weight: 700;
  font-size: 12px;
  line-height: 16px;
  text-transform: uppercase;
  background-color: var(--purple-color);
  color: var(--white-color);
  position: relative;
  height: min-content;
  border-radius: var(--border-radius-verty-small);
  transition: background-color var(--transition-duration-fast);
}
.statistics__more-link:hover {
  background-color: var(--purple-color-hover);
}
.statistics__more-link:before {
  position: absolute;
  content: "";
  top: 50%;
  right: 4px;
  width: 12px;
  height: 12px;
  transform: translateY(-50%) rotate(-90deg);
  background-image: url("../img/arrow-wite.svg");
  background-size: contain;
  background-repeat: no-repeat;
}
.statistics__swiper-wrapper {
  padding-bottom: 56px;
}
