  overflow-x: auto;
  scrollbar-width: none;
  -ms-overflow-style: none;
}
.statistics__table-container--scrollable::-webkit-scrollbar * {
  background: transparent;
}
.statistics__table-container--scrollable::-webkit-scrollbar-track {
  background-color: transparent;
}
.statistics__table-container--scrollable::-webkit-scrollbar {
  display: none;
  background-color: transparent;
}
.statistics__table-container--scrollable::-webkit-scrollbar-thumb {
  background-color: transparent;
}
